.container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    left : 30%;
    width: 600px;
    height: 450px;
}

.cur{
    border-radius: 0.5rem;
    box-shadow: 0px 0px 7px #666;
    width: 100%;
    height: 100%;
  }

.arrow {
    position: absolute;
    width: 2rem;
    height: 2rem;
    color: #fff;
    filter: drop-shadow(0px 0px 5px #555);
    cursor: pointer;
}

.arrow-left {
    left: 1rem;
}

.arrow-right {
    right: 1rem;
}
.circle-con{
    display: flex;
    position: absolute;
    bottom : 1rem;
}
.cur-slide{
    background-color: white;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    border: none;
    outline: none;
    margin: 0 0.2rem;
    cursor : pointer;
}
.hid{
    display : none;
}
.hid-slide{
    background-color: gray;
}